import { Injectable, signal } from '@angular/core';
import { MGRUser } from '../interfaces/mgr.interface';

@Injectable({
  providedIn: 'root',
})
export class MgrStoreService {
  #members = signal<MGRUser[]>([]);

  constructor() {}

  get members() {
    return this.#members;
  }

  setMembers(data: MGRUser[]) {
    this.#members.set(data);
  }
}
